export function strip(html){
    let DomParser = require('dom-parser');
    let parser = new DomParser();
    let doc = parser.parseFromString('<div id="_toParse">'+html+"</div>", 'text/html');
    return doc.getElementById('_toParse')?.textContent || "";
 }

export const mdToTxt = (md)=>{
    if( !md ){
        return '';
    }

    let html = marked(md);
    return strip(html);
}

export const firstNCharts = (str, nChart) =>{
    if( !str || !str.length || str.length<nChart){
        return str;
    }

    return str.substring(0,nChart)+' ... ';
};

export const mdRenderLinkInNewTab = (props) => {
    if (!props.href.startsWith('http')) {
      return props.children;
    }
  
    return <a href={props.href} rel='nofollow noreferrer noopener' target='_blank'>{props.children}</a>;
};

export const mdBuildImgRenderer = (article_content_images) =>{
    return ({alt,src,title})=>{
            const image = article_content_images.find(element => element.base === src );      
            if( alt && alt.toLowerCase().startsWith("_fix_") ){
                alt = alt.substring(5);
                return <GImg fixed={image.childImageSharp.fixed} alt={alt} title={title}/>;
            }else{
                return <GImg fluid={image.childImageSharp.fluid} alt={alt} title={title}/>;
            }     
            
    };

}